/* We need to add display:inline in order to align the '>>' of the 'read more' link */
.post-excerpt p {
    display: inline;
}

// Import partials from `sass_dir` (defaults to `_sass`)
@import "syntax", "starsnonscss";

.about-social a {
    font-size: 200%;
    margin-right: 5px;
}

.embed-video-container {
    margin: 1em 0;
    width: 100%;
    position: relative;
    iframe {
        margin: 0;
        margin-left: auto;
        margin-right: auto;
        border: 0;
        display: block;
    }
}

.container .series-wrapper {
    text-align: center;
    > .article-collection {
        display: inline-block;
        -webkit-user-select: none;
        -moz-user-select: none;
        -ms-user-select: none;
        user-select: none;
        font-size: 16px;
        overflow: hidden;
        width: 92%;
        max-width: 718px;
        margin-bottom: 1.1vw;
        position: relative;
        z-index: 8;
        > a {
            color: #0a0a0a;
            padding: calc(0.3vw + 14px);
            background: #d8d8de;
            display: inline-block;
            position: relative;
            z-index: 4;
            max-width: 260px;
            margin: 0.2vw;
            border-radius: 100px;
            &:hover {
                background: lighten(#00ab6b, 20%);
            }
            &.current-article {
                background: #00ab6b;
            }
        }
    }
}

.article-post {
    p > img {
        margin-left: auto;
        margin-right: auto;
        display: block;
    }
}

.book-company-logo {
    margin: 0px 30px 0px 0px;
}
.book-img {
    width: 60%;
    margin: auto;
    margin-top: 10px;
    margin-bottom: 15px;
    -webkit-box-shadow: 10px 10px 20px -10px rgba(0, 0, 0, 0.75);
    -moz-box-shadow: 10px 10px 20px -10px rgba(0, 0, 0, 0.75);
    box-shadow: 10px 10px 20px -10px rgba(0, 0, 0, 0.75);
}

.link-container {
    margin: 0.95em auto;
    border: 1px solid #dbdbdb;
    -webkit-box-shadow: 1px 2px 4px 0 rgba(0, 0, 0, 0.18);
    -moz-box-shadow: 1px 2px 4px 0 rgba(0, 0, 0, 0.18);
    box-shadow: 1px 2px 4px 0 rgba(0, 0, 0, 0.18);
    border-radius: 3px;
    max-width: 90%;
    padding: 10px;
    .ltag__link__content {
        h3,
        p {
            margin-bottom: 1rem;
        }
    }
}

.jekyll-twitter-plugin {
    margin: 1rem auto;
    twitter-widget {
        margin: 1.5rem auto;
    }
    .twitter-tweet-rendered {
        margin-left: auto;
        margin-right: auto;
    }
}

.profile-pic {
    background-color: transparent;
    width: 100%;
    min-height: 300px;
    perspective: 1000px;
    > .flip-card-inner {
        position: relative;
        width: 100%;
        height: 100%;
        text-align: center;
        transition: transform 0.6s;
        transform-style: preserve-3d;
        box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
        border-radius: 50%;
        > .flip-card-front,
        > .flip-card-back {
            position: absolute;
            width: 100%;
            height: 100%;
            -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
            border-radius: 50%;
            > img {
                border-radius: 50%;
                width: 100%;
                max-height: 300px;
                max-width: 300px;
            }
        }
        > .flip-card-front {
            background-color: #bbb;
            color: black;
        }

        > .flip-card-back {
            background-color: #2980b9;
            color: white;
            transform: rotateY(180deg);
        }
    }
    &:hover .flip-card-inner {
        transform: rotateY(180deg);
    }
}

.no-image {
    .image-block {
        display: none;
    }
    .col-lg-7 {
        flex: 0 0 100%;
        max-width: 100%;
    }
}

.tag-title {
    h2 {
        padding-top: 75px;
    }
}

/* Popover styling */
.popover-wrapper {
    position: relative;
    &:hover .popover-content {
        position: absolute;
        z-index: 10;
        opacity: 1;
        visibility: visible;
        transform: translate(0, -20px);
        transition: all 0.5s cubic-bezier(0.75, -0.02, 0.2, 0.97);
    }

    .popover-content {
        opacity: 0;
        display: inline;
        visibility: hidden;
        position: absolute;
        left: 0px;
        transform: translate(0, 10px);
        width: 70vw;
        height: 60vh;
        overflow-y: auto;
        background-color: #fff;
        box-shadow: 10px 10px 20px -10px rgba(0, 0, 0, 0.75);
        border: 1px solid rgba(0, 0, 0, 0.125);
        border-radius: 0.25rem;
        padding: 1rem;

        &:before {
            position: absolute;
            z-index: -1;
            content: "";
            left: 60px;
            top: -10px;
            border-style: solid;
            border-width: 0 10px 10px 10px;
            border-color: transparent transparent rgba(0, 0, 0, 0.125) transparent;
            transition-duration: 0.3s;
            transition-property: transform;
        }
    }

    .popover-title {
        font-size: 15px;
        line-height: 36px;
        text-decoration: none;
        text-align: center;
        padding: 15px 0;
    }
}

@media screen and (min-width: 768px) {
    .popover-wrapper {
        .popover-content {
            width: 50vw;
        }
    }
}

@media screen and (min-width: 992px) {
    .popover-wrapper {
        .popover-content {
            width: 40vw;
        }
    }
}

@media screen and (min-width: 1200px) {
    .popover-wrapper {
        .popover-content {
            width: 30vw;
        }
    }
}

// Carbon ads
#carbonads {
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen-Sans, Ubuntu, Cantarell, "Helvetica Neue", Helvetica, Arial,
        sans-serif;
    display: flex;
    background-color: hsl(0, 0%, 98%);
    box-shadow: 0 1px 4px 1px hsla(0, 0%, 0%, 0.1);
    margin: 0 auto;
    span {
        position: relative;
        display: block;
        overflow: hidden;
        width: 100%;
        a {
            color: inherit;
            text-decoration: none;
            &:hover {
                color: inherit;
            }
        }

        .carbon-wrap {
            display: flex;
            .carbon-img {
                display: block;
                margin: 0;
                line-height: 1;
                img {
                    display: block;
                    margin: auto;
                    min-width: 130px;
                    max-width: 150px;
                }
            }

            .carbon-text {
                font-size: 13px;
                padding: 10px;
                line-height: 1.5;
                text-align: left;
            }
        }

        .carbon-poweredby {
            display: block;
            padding: 8px 10px;
            background: repeating-linear-gradient(
                    -45deg,
                    transparent,
                    transparent 5px,
                    hsla(0, 0%, 0%, 0.025) 5px,
                    hsla(0, 0%, 0%, 0.025) 10px
                )
                hsla(203, 11%, 95%, 0.4);
            text-align: center;
            text-transform: uppercase;
            letter-spacing: 0.5px;
            font-weight: 600;
            font-size: 8px;
            line-height: 1;
            color: #8c8a8a;
        }
    }
}

.ad-holder {
    max-width: 100%;
}

@media screen and (min-width: 992px) {
    .ad-holder {
        max-width: 300px;
    }
}
@media screen and (min-width: 992px) {
    .ad-vertical-md {
        #carbonads {
            max-width: 135px;
            .carbon-wrap {
                flex-direction: column;
                .carbon-img {
                    img {
                        max-width: 100% !important;
                    }
                }
            }
        }
    }
}
